export const storeUser = (data) =>{
    return{
        type: "storeUserData",
        userData: data
    }
}
export const restaurantData = (data) =>{
    return{
        type: "storeRestaurantData",
        restaurantData: data
    }
}
export const cartData = (cartData) =>{
    return{
        type: "cartData",
        cartData: cartData
    }
}

export const userData = (userData) =>{
    return{
        type: "storeUserData",
        userData: userData
    }
}

